@media only screen and (max-width: 1920px) {
  .posts-container {
    column-gap: 10px;
  }
}

@media only screen and (max-width: 1860px) {
  .main {
    padding: 3rem 17rem 10rem;
  }

  .search-container {
    right: 17rem;
  }
}

@media only screen and (max-width: 1640px) {
  .main {
    padding: 3rem 11rem 10rem;
  }

  .posts-container {
    justify-content: center;
  }

  .search-container {
    right: 11rem;
  }
}

@media only screen and (max-width: 1455px) {
  .main {
    padding: 3rem 8rem 10rem;
  }

  .search-container {
    right: 8rem;
  }
}

@media only screen and (max-width: 1356px) {
  .main {
    padding: 3rem 3rem 10rem;
  }

  .search-container {
    right: 3rem;
  }

  .post {
    flex: 1 0 50%;
  }

  .search-container input {
    width: 70vw;
  }
}

@media only screen and (max-width: 1195px) {
  .posts-container {
    column-gap: 30px;
  }
}

@media only screen and (max-width: 846px) {
  .main {
    padding: 3rem 0 10rem;
  }

  .search-container input {
    width: 85vw;
  }
}

@media only screen and (max-width: 670px) {
  .post {
    flex: 1 0 100%;
  }

  .upperHeader {
    position: sticky;
    background-color: white;
    top: 0;
    border-bottom: none;
  }

  .search-container input {
    width: 70vw;
  }

  .main {
    padding: 0rem 1.25rem 10rem;
  }

  .mobileOnly {
    display: block;
  }

  .stickyHeader {
    padding: 2rem;
    width: 85%;
    height: 100vh;
    position: fixed;
    left: 0;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    border: 1px solid #E9E9E9;
    transition: left 0.7s ease;
  }

  .menu {
    width: 100%;
    display: flex;
    flex-direction: column;
  }

  .menu__item {
    border-bottom: 1px solid #E9E9E9;
    width: 100%;
  }

  .menu__item > a {
    display: flex;
    align-items: center;
    padding: 1.25rem 0;
  }

  .mobileLogo {
    padding-bottom: 2rem;
    width: 100%;
    border-bottom: 1px solid #E9E9E9;
    display: flex;
    justify-content: space-between;
  } 

  .mobileLogo > .icon-close {
    padding: 13px;
  }
  
  .mobileLogo > .icon-close:before, .icon-close:after {
    width: 20px;
    height: 4px;
    border-radius: 5px;
  }

  .mobileStickyHeader {
    left: -100%;
  }

  .icon-burger {
    display: inline-block;
    width: 23px;
    height: 4px;
    background-color: #000;
    position: relative;
    cursor: pointer;
    border-radius: 5px;
  }
  
  .icon-burger::before,
  .icon-burger::after {
    content: "";
    position: absolute;
    width: 100%;
    height: 4px;;
    background-color: #000;
    border-radius: 5px;
  }
  
  .icon-burger::before {
    top: -8px;
  }
  
  .icon-burger::after {
    top: 8px;
  }

  .burgerIconContainer {
    position: absolute;
    top: 1.8rem;
    left: 3rem;
  }
}

@media only screen and (max-width: 450px) {
  .main {
    padding: 0 1.25rem 10rem;
  }

  .burgerIconContainer {
    left: 1.25rem;
  }

  .search-container {
    right: 1.25rem;
  }
}
